'use client'
import styles from "../../page.module.css";
import Form from 'react-bootstrap/Form';
import EntryBox from "../../_components/entrybox";
import Submit from "../../_components/submit";
import Select from "../../_components/select";
import { useEffect, useReducer } from "react";
import CheckBox from "../../_components/checkbox";
import { fetch } from "../../_scripts/communications";
import backgroundImage from "../../../public/Green.png"
import FormTitle from "@/app/_components/formtitle";
import Feedback from "@/app/_components/feedback";
import MultiSelect from "@/app/_components/multiselect";
import SelectEntry from "@/app/_components/selectentry";
import FormDescription from "@/app/_components/formdescription";

let pageDescription = ['Cancel an LCS Classroom session and unenroll all learners.']

let submitObject = {};

const initState = {
    'Select Course':{
        options: [{ID:0, Name:''}],
        value:0
    },
    'Trainer':{
        options: [{ID:0,Name:''}],
        value:0
    },
    'Remote Trainer':{
        options: [],
        value:0
    },
    'Room':{
        options: [],
        value:236
    },
    'Shadow Name': {
        value: '',
    },
    'Teams Link': {
        value: '',
    },
    'Session Date': {
        value: ''
    },
    'Session Length': {
        value: 60
    },
    'Select Students':{
        options: [],
        value:[]
    },
    'Has Shadow':{
        isChecked: false
    },
    'Needs Reserved':{
        isChecked: false
    },
    'Has Remote':{
        isChecked: false
    },
    'Assets List':[],
    'Sessions List':[],
    'Trainers List':[],
    'Submit':{
        Color:'danger',
        Text:'Cancel Class'
    },
    'Feedback':{
        color:'success',
        code:'',
        title:'',
        text:'',
        hidden: true
    }
};

const reducer = (state, action)=>{

    let newState = {...state};

    switch (action.type){

        case 'update-Value':{

            console.dir(action);

            newState[action.label].value = action.value;

            return newState

            break
        }

        case 'mark-Checked':{

            newState[action.label].isChecked = action.value;

            return newState

            break;

        }

        case 'load-SelectBox':{

            let Sessions = action.data.Sessions;

            newState['Select Course'].options = Sessions;

            newState['Select Course'].value = Sessions[0].ID;

            return newState

            break

        }

        case 'load-Response':{

            let Learners = action.data.data.Learners;

            newState['Select Students'].options = Learners;

            newState['Session Date'].value = action.data.data.sessionDate;

            return newState

            break;

        }

        case 'submit-Clicked':{

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Error':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Success':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;

        }
        case 'load-Feedback':{

            newState['Feedback'].hidden = false;
            newState['Feedback'].color = action.color;
            newState['Feedback'].code = action.code;
            newState['Feedback'].title = action.title;
            newState['Feedback'].text = action.text;

            return newState;

            break;

        }

    }
};

export default function CancelClass() {

    const [state, dispatch] = useReducer(reducer,initState);



    useEffect(function(){

                const loadDropListOptions = async()=>{

                    let options = await fetch({method:'GET',endpoint:'Litmos/RetrieveUpcomingSessions'});

                    if(options.didError){

                        //Do stuff

                        return

                    }

                    dispatch({type:'load-SelectBox',data:options.data})

                }

                loadDropListOptions();
            
                return

            },[]
    );

    submitObject = {
        MasterTicket: state['Select Course'].value}

  return (
    <main className={styles.main} style={{backgroundImage: `url(${backgroundImage.src})`}}>
      <Form>
        <FormTitle text='Cancel a Class'></FormTitle>
        <FormDescription text={pageDescription}></FormDescription>
        <Select
            options={state['Select Course'].options}
            label='Select Course'
            value={state['Select Course'].value}
            dispatch={dispatch}
        ></Select>
        <Submit 
            method="POST"
            endpoint="Litmos/CancelClass"
            dispatch={dispatch}
            data={submitObject}
            color={state.Submit.Color}
            text={state.Submit.Text}
            ></Submit>
            <Feedback
                color={state['Feedback'].color}
                code={state['Feedback'].code}
                title={state['Feedback'].title}
                text={state['Feedback'].text}
                hidden={state['Feedback'].hidden}
            ></Feedback>
      </Form>
    </main>
  );
}
