'use client'
import '../../public/css/bootstrap.css';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import { fetch } from "../_scripts/communications";


function SelectEntry(p) {

    const {label,value,dispatch,hidden=false,endpoint,data,options,text='Load'} = p;

  return (
      <Form.Group hidden={hidden} className="mb-3" controlId={`form${label}`}>
        <InputGroup className="mb-3">
            <Button 
            onClick={async()=>{
              let response = await fetch({method:"POST",endpoint:endpoint,data:data});
              dispatch({type:'load-Response',data:response})
            }}
            variant="secondary" id="button-addon1">
            {text}
            </Button>
            <Form.Select className='' aria-label={label} value={value} onChange={e=>dispatch({type:'update-Value',value:e.target.value, label:label})}>
              {options.map(o=>{return <option key={o.ID} value={o.ID}>{o.Name}</option>})}
            </Form.Select>
        </InputGroup>
      </Form.Group>
  );
}

export default SelectEntry;